<template>
  <div id="view-employee">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>{{ name }}</h4>
      </li>
      <li class="collection-item">Employee ID#: {{ employee_id }}</li>
      <li class="collection-item">Department: {{ dept }}</li>
      <li class="collection-item">Position: {{ position }}</li>
    </ul>
    <router-link class="btn gray" to="/">Back</router-link>
    <button @click="deleteEmployee" class="btn red" to="/">Delete</button>

    <div class="fixed-action-btn">
      <router-link
        :to="{name: 'edit-employee', params: {employee_id}}"
        class="btn-floating btn-large red"
      >
        <i class="fa fa-pencil"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit";

export default {
  name: "view-employee",
  data() {
    return {
      employee_id: null,
      name: null,
      dept: null,
      position: null
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("employees")
      .where("employee_id", "==", to.params.employee_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(employee => {
          next(vm => {
            vm.employee_id = employee.data().employee_id;
            vm.position = employee.data().position;
            vm.dept = employee.data().dept;
            vm.name = employee.data().name;
          });
        });
      });
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      db.collection("employees")
        .where("employee_id", "==", this.$route.params.employee_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(employee => {
            this.employee_id = employee.data().employee_id;
            this.position = employee.data().position;
            this.dept = employee.data().dept;
            this.name = employee.data().name;
          });
        });
    },
    deleteEmployee() {
      if (confirm("Are you sure ?")) {
        db.collection("employees")
          .where("employee_id", "==", this.$route.params.employee_id)
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(employee => {
              employee.ref.delete();
              this.$router.push("/");
            });
          });
      }
    }
  }
};
</script>

