<template>
  <div id="edit-employee">
    <h3>Edit employee</h3>

    <div class="row">
      <form @submit.prevent="updateEmployee" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input id="id" type="text" v-model="employee_id" disabled />
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input id="name" type="text" v-model="name" required />
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input id="dept" type="text" v-model="dept" required />
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input id="position" type="text" v-model="position" required />
          </div>
        </div>
        <button type="submit" class="btn">Submit</button>
        <router-link to="/" class="btn grey">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit";

export default {
  name: "edit-employee",
  data() {
    return {
      employee_id: null,
      name: null,
      dept: null,
      position: null
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("employees")
      .where("employee_id", "==", to.params.employee_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(employee => {
          next(vm => {
            vm.employee_id = employee.data().employee_id;
            vm.position = employee.data().position;
            vm.dept = employee.data().dept;
            vm.name = employee.data().name;
          });
        });
      });
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      db.collection("employees")
        .where("employee_id", "==", this.$route.params.employee_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(employee => {
            this.employee_id = employee.data().employee_id;
            this.position = employee.data().position;
            this.dept = employee.data().dept;
            this.name = employee.data().name;
          });
        });
    },
    updateEmployee() {
      db.collection("employees")
        .where("employee_id", "==", this.$route.params.employee_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref
              .update({
                employee_id: this.employee_id,
                name: this.name,
                dept: this.dept,
                position: this.position
              })
              .then(() => {
                this.$router.push("/");
              })
              .catch(err => console.log(err));
          });
        });
    }
  }
};
</script>

